import $ from 'jquery';
import { default as BSUtil } from 'bootstrap/js/src/util';

const Util = {

  ...BSUtil,

  getRootCss(propertyName) {
    return $(document.documentElement).css(propertyName);
  },

  getBreakpointCss(breakpoint) {
    return this.getRootCss(`--breakpoint-${breakpoint}`);
  },

  getBreakpointWidth(breakpoint) {
    return Number.parseInt(this.getBreakpointCss(breakpoint), 10);
  }
};

export default Util;
