import $ from 'jquery';
import Util from './util';

/**
 * ------------------------------------------------------------------------
 * Constants
 * ------------------------------------------------------------------------
 */

const NAME = 'navbar';
const VERSION = '0.0.1';
const DATA_KEY = 'bs.navbar';
const DATA_KEY_COLLAPSE = 'bs.collapse';
const EVENT_KEY = `.${DATA_KEY}`;
const EVENT_KEY_COLLAPSE = `.${DATA_KEY_COLLAPSE}`;
const DATA_API_KEY = '.data-api';
const JQUERY_NO_CONFLICT = $.fn[NAME];

const Default = {
  scrollTop: false
};

const DefaultType = {
  scrollTop: 'boolean'
};

const EVENT_OPEN = `open${EVENT_KEY}`;
const EVENT_OPENED = `opened${EVENT_KEY}`;
const EVENT_CLOSE = `close${EVENT_KEY}`;
const EVENT_CLOSED = `closed${EVENT_KEY}`;
const EVENT_SHOW_COLLAPSE = `show${EVENT_KEY_COLLAPSE}${EVENT_KEY}`;
const EVENT_SHOW_COLLAPSE_DATA_API = `${EVENT_SHOW_COLLAPSE}${DATA_API_KEY}`;
const EVENT_SHOWN_COLLAPSE = `shown${EVENT_KEY_COLLAPSE}${EVENT_KEY}`;
const EVENT_SHOWN_COLLAPSE_DATA_API = `${EVENT_SHOWN_COLLAPSE}${DATA_API_KEY}`;
const EVENT_HIDE_COLLAPSE = `hide${EVENT_KEY_COLLAPSE}${EVENT_KEY}`;
const EVENT_HIDE_COLLAPSE_DATA_API = `${EVENT_HIDE_COLLAPSE}${DATA_API_KEY}`;
const EVENT_HIDDEN_COLLAPSE = `hidden${EVENT_KEY_COLLAPSE}${EVENT_KEY}`;
const EVENT_HIDDEN_COLLAPSE_DATA_API = `${EVENT_HIDDEN_COLLAPSE}${DATA_API_KEY}`;

const CLASS_NAME_OPENING = 'opening';
const CLASS_NAME_OPEN = 'open';
const CLASS_NAME_CLOSING = 'closing';

const SELECTOR_NAVBAR = '.navbar';
const SELECTOR_NAVBAR_COLLAPSE = '.navbar-collapse';

/**
 * ------------------------------------------------------------------------
 * Class Definition
 * ------------------------------------------------------------------------
 */

class Navbar {
  constructor(element, config) {
    this._$html = $(document.documentElement);
    this._$element = $(element);
    this._$collapseNavbarElement = this._$element.find(SELECTOR_NAVBAR_COLLAPSE);
    this._config = this._getConfig(config);

    this._$collapseNavbarElement.on(EVENT_SHOW_COLLAPSE_DATA_API, this._onShowNavbarCollapse.bind(this));
    this._$collapseNavbarElement.on(EVENT_SHOWN_COLLAPSE_DATA_API, this._onShownNavbarCollapse.bind(this));
    this._$collapseNavbarElement.on(EVENT_HIDE_COLLAPSE_DATA_API, this._onHideNavbarCollapse.bind(this));
    this._$collapseNavbarElement.on(EVENT_HIDDEN_COLLAPSE_DATA_API, this._onHiddenNavbarCollapse.bind(this));

    console.info('Successfully constructed navbar.');
  }

  // Getters

  static get VERSION() {
    return VERSION;
  }

  static get Default() {
    return Default;
  }

  // Public

  open() {
    this._$collapseNavbarElement.collapse('show');
  }

  close() {
    this._$collapseNavbarElement.collapse('hide');
  }

  // Private

  _getConfig(config) {
    config = {
      ...Default,
      ...config
    };
    config.scrollTop = Boolean(config.scrollTop);
    Util.typeCheckConfig(NAME, config, DefaultType);
    return config;
  }

  _onShowNavbarCollapse() {
    if (this._config.scrollTop) {
      this._$html.animate({
        scrollTop: Math.ceil($(this._$element).position().top)
      });
    }

    const openEvent = $.Event(EVENT_OPEN);

    this._$element
      .trigger(openEvent)
      .addClass(CLASS_NAME_OPENING);
  }

  _onShownNavbarCollapse() {
    const openedEvent = $.Event(EVENT_OPENED);

    this._$element
      .addClass(CLASS_NAME_OPEN)
      .removeClass(CLASS_NAME_OPENING)
      .trigger(openedEvent);
  }

  _onHideNavbarCollapse() {
    const closeEvent = $.Event(EVENT_CLOSE);

    this._$element
      .trigger(closeEvent)
      .addClass(CLASS_NAME_CLOSING)
      .removeClass(CLASS_NAME_OPEN);
  }

  _onHiddenNavbarCollapse() {
    const closedEvent = $.Event(EVENT_CLOSED);

    this._$element
      .removeClass(CLASS_NAME_CLOSING)
      .trigger(closedEvent);
  }

  // Static

  static _jQueryInterface(config) {
    return this.each(function () {
      const $element = $(this);
      let data = $element.data(DATA_KEY);
      const _config = {
        ...Default,
        ...$element.data(),
        ...(typeof config === 'object' && config ? config : {})
      };

      if (!data) {
        data = new Navbar(this, _config);
        $element.data(DATA_KEY, data);
      }

      if (typeof config === 'string') {
        if (typeof data[config] === 'undefined') {
          throw new TypeError(`No method named "${config}"`);
        }

        data[config]();
      }
    });
  }
}

/**
 * ------------------------------------------------------------------------
 * Data Api implementation
 * ------------------------------------------------------------------------
 */

$(() => {
  $(SELECTOR_NAVBAR).each(function () {
    const $element = $(this);
    const config = $element.data();
    Navbar._jQueryInterface.call($element, config);
  });
});

/**
 * ------------------------------------------------------------------------
 * jQuery
 * ------------------------------------------------------------------------
 */

$.fn[NAME] = Navbar._jQueryInterface;
$.fn[NAME].Constructor = Navbar;
$.fn[NAME].noConflict = () => {
  $.fn[NAME] = JQUERY_NO_CONFLICT;
  return Navbar._jQueryInterface;
};

export default Navbar;
